header {
    padding-top: 3em;
    position: relative;

    .header-wrapper {
        display: flex;
        flex-direction: row;
        align-items: center;
        align-content: center;
        justify-content: space-between;
        z-index: 3;
        position: relative;

        nav,
        .menu {
            flex: 1;
            text-transform: uppercase;
            font-weight: 400;

            .active {
                color: $orange;
                transition: ease all .3s;

                &::after {
                    --scale: 1;

                    content: '';
                    position: absolute;
                    left: 0;
                    right: 0;
                    top: 100%;
                    height: 3px;
                    background: $orange;
                    transform: scaleX(var(--scale));
                    transform-origin: var(--x) 50%;
                    transition: transform .5s cubic-bezier(.535, .05, .355, 1);
                }
            }

            a {
                position: relative;
                text-decoration: none;
                color: $white;
                transition: color .5s ease;

                &:hover {
                    color: $orange;
                }

            }

            .page-link+.page-link {
                margin-left: 20px;
            }
        }

        nav {


            a {
                transition: ease all .5s;

                &:active {
                    color: $orange;

                    &::after {
                        --scale: 1;
                    }
                }

                &::after {
                    --scale: 0;

                    content: '';
                    position: absolute;
                    left: 0;
                    right: 0;
                    top: 100%;
                    min-width: 3px;
                    height: 3px;
                    background: $orange;
                    transform: scaleX(var(--scale));
                    transform-origin: var(--x) 50%;
                    transition: transform .5s cubic-bezier(.535, .05, .355, 1);

                }

                &:hover {
                    transition-delay: 100ms;
                    color: $orange;
                }

                &:hover::after {
                    transition-delay: 100ms;
                    --scale: 1;
                }

                &:active {
                    color: $orange;

                    &::after {

                        --scale: 1;
                    }
                }
            }

            #jobs-dropdown {
                position: relative;
                display: inline;
                .dropdown-wrapper {
                    filter: opacity(0);
                    position: absolute;
                    background-color: transparent;
                    min-width: 160px;
                    box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
                    border-top: 1em solid rgba(255, 255, 255, 0);
                    z-index: 1;
                    left: 0px;
                    transition: ease all 230ms;
pointer-events: none;


                    .jobs-menu {
                        padding: 1em;
                        min-width: 200px;
                        display: flex;
                        flex-direction: column;
                        background-color: rgba($lighter-blue, 75%);
                        border: 2px solid $orange;
                        position: relative;

                        a {
                            transition: ease all 230ms;
                            color: $white;

                            &:hover {
                                color: $orange;
                            }

                            &::after {
                                content: '';
                                position: absolute;
                                height: 0px;
                                transform-origin: 0%;
                            }

                            &+a {
                                margin-top: 10px;
                            }
                        }

                    }
                }

                &:hover {
                    color: $orange;

                    .dropdown-wrapper {
                        filter: opacity(1);
                        pointer-events: all;
                    }
                }
            }
        }

        .menu {
            display: flex;
            justify-content: flex-end;

            a {
                display: flex;
                flex-direction: row;
                align-items: center;
                transition: ease all 230ms;

                i {
                    margin-right: 8px;
                }

                &+a {
                    margin-left: 20px;

                }
            }

            .apply-link {
                z-index: 1;
                position: relative;
                font-size: inherit;
                font-family: inherit;
                color: white;
                padding: 0.8em 1em;
                outline: none;
                border: none;
                background-color: none;
                overflow: hidden;
                cursor: pointer;
                border: 2px solid $white;

                &::after {
                    content: '';
                    z-index: -1;
                    background-color: hsla(0, 0%, 100%, 0.1);
                    position: absolute;
                    top: -50%;
                    bottom: -50%;
                    width: 1.25em;
                    transform: translate3d(-525%, 0, 0) rotate(35deg);
                }

                &:hover {
                    background: rba($white, 15%);

                    &::after {
                        transition: transform 0.45s ease-in-out;
                        transform: translate3d(800%, 0, 0) rotate(35deg);
                    }
                }
            }
        }

        .menu-apply {
            display: flex;
            justify-content: flex-end;
            flex: 1;
            text-transform: uppercase;
            font-weight: 400;

            #contact-link {
                position: relative;
                text-decoration: none;
                color: $white;
                transition: color .5s ease;
                transition: ease all .5s;

                &:active {
                    color: $orange;

                    &::after {
                        --scale: 1;
                    }
                }

                &::after {
                    --scale: 0;

                    content: '';
                    position: absolute;
                    left: 0;
                    right: 0;
                    top: 100%;
                    min-width: 3px;
                    height: 3px;
                    background: $orange;
                    transform: scaleX(var(--scale));
                    transform-origin: var(--x) 50%;
                    transition: transform .5s cubic-bezier(.535, .05, .355, 1);

                }

                &:hover {
                    transition-delay: 100ms;
                    color: $orange;
                }

                &:hover::after {
                    transition-delay: 100ms;
                    --scale: 1;
                }

                &:active {
                    color: $orange;

                    &::after {

                        --scale: 1;
                    }
                }

                &.active {
                    color: $orange;
                    // border-bottom: 3px solid $orange;
                    transition: ease all .3s;

                    &::after {
                        --scale: 1;

                        content: '';
                        position: absolute;
                        left: 0;
                        right: 0;
                        top: 100%;
                        height: 3px;
                        background: $orange;
                        transform: scaleX(var(--scale));
                        transform-origin: var(--x) 50%;
                        transition: transform .5s cubic-bezier(.535, .05, .355, 1);
                    }

                    // &::before {
                    //     content: "" !important;
                    //     position: absolute;
                    //     left: 50%;
                    //     top: -20px;
                    //     color: $orange;
                    //     // font-weight: 900;
                    // }
                }
            }



            .apply-cta-link {
                position: relative;
                margin-right: 20px;

                .apply-link {
                    background-color: none;
                    color: $white;
                    padding: 0.8em 1em;
                    border: 1px solid $orange;
                    transition: all 0.3s ease-in-out;

                    i {
                        margin-right: 8px;
                    }

                    &:hover {
                        background: rgba($white, 10%);

                    }
                }

                span {
                    position: absolute;
                    background-color: $orange;
                    transform-origin: center;
                    transition: transform 0.5s ease;

                    span.bottom,
                    span.top {
                        height: 3px;
                        left: 0;
                        right: 0;
                        transform: scaleX(0);
                    }

                    span.left,
                    span.right {
                        width: 3px;
                        top: 0;
                        bottom: 0;
                        transform: scaleY(0);
                    }

                    span.top {
                        top: -1em;
                    }

                    span.bottom {
                        bottom: -1em;
                    }

                    span.left {
                        left: -1em;
                    }

                    span.right {
                        right: -1em;
                    }
                }


            }
        }

        svg,
        img {
            max-width: 200px;
            flex-grow: 1;
        }
    }
}